// 
const havenSign = ''

const home = {
	state: {
       sign:''
	},
	mutations: {
		getData (state) {
			state.sign = havenSign
		}
	},
	actions: {
		getData ({commit}) {
			commit('getData')
		},
		getApi ({commit}) {
			commit('getApi')
		}
	},
	getters: {
		
	}
}
export default home



